@-webkit-keyframes spawnWord {
    from {
        opacity: 0;
        -webkit-transform: translateY(-4rem) scale(0.2);
                transform: translateY(-4rem) scale(0.2);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
}

@keyframes spawnWord {
    from {
        opacity: 0;
        -webkit-transform: translateY(-4rem) scale(0.2);
                transform: translateY(-4rem) scale(0.2);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
}

@-webkit-keyframes wordLeave {
    from {
        opacity: 1;
        -webkit-transform: translateY(-4rem) scale(1);
                transform: translateY(-4rem) scale(1);
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(1rem) scale(0);
                transform: translateY(1rem) scale(0);
        display: none;
    }
}

@keyframes wordLeave {
    from {
        opacity: 1;
        -webkit-transform: translateY(-4rem) scale(1);
                transform: translateY(-4rem) scale(1);
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(1rem) scale(0);
                transform: translateY(1rem) scale(0);
        display: none;
    }
}

.wordEnter {
    font-size: 3rem;
    -webkit-animation: spawnWord 1s 1;
            animation: spawnWord 1s 1;
}

.wordLeave {
    font-size: 3rem;
    -webkit-transform: translateY(-4rem);
            transform: translateY(-4rem);
    -webkit-animation: wordLeave 1s forwards;
            animation: wordLeave 1s forwards;
}

